<template>
    <div class="box">
      <header>
        <NavBar :blob="700">
          <template #title>我的订单</template>
        </NavBar>
      </header>
      <main>
        <van-tabs v-model="active" line-width="6vw">
          <van-tab v-for="item in information" :key="item.value" :title="item.name" :name="item.value">
            <div v-if="timeShow && orderList.length !== 0">
              <orderBigItem v-for="item in orderList" :key="item.order_id" :order="item" @click="getOrderListAction(active, 2)"></orderBigItem>
            </div>
            <!-- 请求时内容全部为空，请求完成后才是否有内容 -->
            <div v-if="timeShow && orderList.length === 0 " class="write">
              <img src="@/assets/空空如也.png" alt="">
              <span>亲，暂无新的订单</span>
            </div>
          </van-tab>
        </van-tabs>
      </main>
    </div>
</template>

<script>
import orderBigItem from '@/components/orderBigItem.vue'
import { getOrderList } from '@/api/order'
import loginConfirm from '@/mixins/loginConfirm'
export default {
  name: 'myorderINdex',
  mixins: [loginConfirm],
  data () {
    return {
      active: 0,
      orderList: [],
      timeShow: false,
      count: 0,
      isLoading: false,
      information: [
        { name: '全部', value: 'all' },
        { name: '待支付', value: 'payment' },
        { name: '待发货', value: 'delivery' },
        { name: '待收货', value: 'received' },
        { name: '待评价', value: 'comment' }
      ]
    }
  },
  components: {
    orderBigItem
  },
  methods: {
    async getOrderListAction (value, num) {
      if (num === 1) {
        this.timeShow = false
        const { data: { list: { data } } } = await getOrderList(value)
        this.orderList = data
        this.timeShow = true
      } else {
        this.timeShow = true
        const { data: { list: { data } } } = await getOrderList(value)
        this.orderList = data
      }
    }
  },
  watch: {
    active (n) {
      if (!this.loginConfirm()) return
      this.getOrderListAction(n, 1)
    }
  },
  created () {
    // 有则使用传的，否则使用默认的
    if (this.$route.query.dataType) {
      this.active = this.$route.query.dataType
    }
  }
}
</script>

<style lang="less" scoped>
.write {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 70px 0 0;
  img {
    width: 200px;
  }
  font-size: 16px;
  color: gray;
}
main {
  overflow: auto;
  position: fixed;
  top: 50px;
  left: 0;
  right: 0;
  bottom: 0;
}
</style>
