<template>
  <div class="orderBigItem">
    <header>
        <span>{{order.create_time}}</span>
        <span>{{order.state_text}}</span>
    </header>
    <main @click="$router.push(`/page/order/detail/${order.order_id}`)">
       <orderItem :goodsList="order.goods"></orderItem>
    </main>
    <footer>
        <p>共{{goods_totle}}件商品，总金额<span>￥</span><span>{{order.total_price}}</span></p>
        <div class="btn" v-if="!(order.state_text === '待取消')">
            <button @click="cancelOrderAction">申请取消</button>
        </div>
        <div v-else class="left">
          取消申请中
        </div>
    </footer>
  </div>
</template>

<script>
import orderItem from '@/components/orderitem.vue'
import { cancelOrder } from '@/api/order'
import { Toast, Dialog } from 'vant'
export default {
  name: 'orderBigItem',
  components: {
    orderItem
  },
  props: {
    order: {
      type: Object,
      default: () => {}
    }
  },
  methods: {
    async cancelOrderAction () {
      Dialog.confirm({
        title: '友情提示',
        message: '您确定取消订单吗'
      })
        .then(async () => {
          const res = await cancelOrder(this.order.order_id)
          Toast(res.message)
          this.$emit('click')
        })
        .catch(() => {

        })
    }
  },
  computed: {
    goods_totle () {
      const a = this.order.goods.reduce((count, item) => item.total_num + count, 0)
      return a
    }
  }
}
</script>

<style lang="less" scoped>
.orderBigItem {
    font-size: 14px;
    margin: 10px;
    border-radius: 5px;
    background-color: #fff;
    padding: 15px;
    header {
        display: flex;
        justify-content: space-between;
        align-items: center;
        color: gray;
        span {
            &:nth-child(2) {
                color: red;
            }
        }
        margin-bottom: 25px;
    }
    footer {
      margin-top: 30px;
        text-align: right;
        p {
          span {
            &:nth-child(1) {
              font-size: 12px;
              margin-left: 5px;
            }
            &:nth-child(2) {
              font-size: 16px;
            }
          }
        }
        .btn {
            margin-top: 15px;
            button {
                width: 84px;
                height: 25px;
                background: transparent;
                border-radius: 5px;
                font-size: 14px;
                border: 1px solid gray;
            }
        }
        .left {
          margin-top: 20px;
          color: gray;
          text-align: left;
        }
    }
}

</style>
