import tokenRequest from '@/utils/tokenRequest'

// 获取订单数据
export function getOrderToDoCounts () {
  return tokenRequest.get('/order/todoCounts')
}
// 获取订单列表
export function getOrderList (dataType) {
  return tokenRequest.get('/order/list', {
    params: {
      dataType,
      page: 1
    }
  })
}

// 取消订单
export function cancelOrder (orderId) {
  return tokenRequest.post('/order/cancel', {
    orderId
  })
}
